'use server';
/**
 * @fileOverview This file defines a Genkit flow for suggesting data corrections based on identified anomalies and inconsistencies in a dataset.
 *
 * - suggestDataCorrections - A function that initiates the data correction suggestion process.
 * - SuggestDataCorrectionsInput - The input type for the suggestDataCorrections function, including the data and audit results.
 * - SuggestDataCorrectionsOutput - The return type for the suggestDataCorrections function, providing suggestions for data cleaning and validation.
 */

import {ai} from '@/ai/ai-instance';
import {z} from 'genkit';

const SuggestDataCorrectionsInputSchema = z.object({
  data: z.string().describe('The dataset to be audited, in string format (e.g., CSV or JSON).'),
  auditResults: z.string().describe('The audit results from the AI data audit, highlighting anomalies and inconsistencies.'),
});
export type SuggestDataCorrectionsInput = z.infer<typeof SuggestDataCorrectionsInputSchema>;

const SuggestDataCorrectionsOutputSchema = z.object({
  suggestions: z.string().describe('Suggestions for data cleaning and validation, based on the audit results.'),
  correctionTable: z.array(
    z.object({
      row: z.number().describe('The row number in the dataset.'),
      column: z.string().describe('The column name.'),
      originalValue: z.string().describe('The original value.'),
      suggestedValue: z.string().describe('The suggested correction.'),
      reason: z.string().describe('The reason for the suggestion.'),
    })
  ).optional().describe('A table of specific corrections that can be applied to the dataset.')
});
export type SuggestDataCorrectionsOutput = z.infer<typeof SuggestDataCorrectionsOutputSchema>;

export async function suggestDataCorrections(input: SuggestDataCorrectionsInput): Promise<SuggestDataCorrectionsOutput> {
  const result = await suggestDataCorrectionsFlow(input);
  
  // Process the audit results to create a structured correction table
  try {
    const auditData = JSON.parse(input.auditResults);
    
    if (auditData && auditData.anomalies && Array.isArray(auditData.anomalies)) {
      interface Anomaly {
        row: number;
        column: string;
        value: string;
        message: string;
        suggestion?: string;
      }
      
      const correctionTable = auditData.anomalies
        .filter((anomaly: Anomaly) => anomaly.suggestion) // Only include anomalies with suggestions
        .map((anomaly: Anomaly) => ({
          row: anomaly.row,
          column: anomaly.column,
          originalValue: anomaly.value,
          suggestedValue: anomaly.suggestion,
          reason: anomaly.message
        }));
      
      if (correctionTable.length > 0) {
        return {
          ...result,
          correctionTable
        };
      }
    }
    
    return result;
  } catch (error) {
    console.error('Error processing audit results for correction table:', error);
    return result;
  }
}

const prompt = ai.definePrompt({
  name: 'suggestDataCorrectionsPrompt',
  input: {
    schema: z.object({
      data: z.string().describe('The dataset to be audited.'),
      auditResults: z.string().describe('The audit results from the AI data audit.'),
    }),
  },
  output: {
    schema: z.object({
      suggestions: z.string().describe('Suggestions for data cleaning and validation.'),
    }),
  },
  prompt: `You are an AI assistant specialized in data quality and Indian name validation.

You have audited a police records dataset and have identified some issues with Indian names.
Based on the data and audit results, suggest data cleaning and validation steps to improve data quality.

IMPORTANT CONTEXT ABOUT INDIAN NAMES:
- North Indian names: Often follow a pattern of [Given name] [Middle name/father's name] [Surname]. Common surnames include Singh, Kumar, Sharma, Verma, Gupta, Jha, Mishra.
- South Indian names: Often follow a different convention like [Initial(s)] [Given name] or [Father's name] [Given name] [Village/caste name].
- Common name variations: Indian names may have multiple valid spellings due to transliteration from different Indian scripts. For example, Krishna/Krsna, Lakshmi/Laxmi.
- Regional influences: Names vary significantly based on region (North, South, East, West) and religion (Hindu, Muslim, Sikh, Christian, etc.)
- Gender-specific patterns: Many Indian names have gender-specific endings and patterns that should be preserved.

Focus on the following when providing corrections:
1. Consistent transliteration (e.g., choose either "sh" or "s" for representing certain sounds)
2. Appropriate capitalization (most Indian names capitalize each part)
3. Proper spacing and hyphenation
4. Correct regional variations
5. Gender-appropriate suggestions

Data:
{{{data}}}

Audit Results:
{{{auditResults}}}

Provide a detailed, helpful analysis organized into these sections:
1. SUMMARY OF ISSUES: Brief overview of the name-related problems found
2. SPECIFIC CORRECTIONS: Table of suggested corrections for each problematic name
3. VALIDATION RECOMMENDATIONS: Advice for improving data collection to prevent these issues
4. DATA QUALITY STEPS: Additional steps that could be taken to improve overall data quality

Suggestions:
`,
});

const suggestDataCorrectionsFlow = ai.defineFlow<
  typeof SuggestDataCorrectionsInputSchema,
  typeof SuggestDataCorrectionsOutputSchema
>(
  {
    name: 'suggestDataCorrectionsFlow',
    inputSchema: SuggestDataCorrectionsInputSchema,
    outputSchema: SuggestDataCorrectionsOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return {
      suggestions: output!.suggestions
    };
  }
);
