// Audit Indian names in a dataset.

'use server';

import {ai} from '@/ai/ai-instance';
import {validateIndianName} from '@/services/indian-name-validator';
import {z} from 'genkit';

// Indian name patterns reference
const INDIAN_NAME_PATTERNS = {
  // Common name patterns by region
  regionPatterns: {
    northIndian: "North Indian names often have Singh, Kumar, Sharma, Verma, Gupta as surnames. First names are often shorter.",
    southIndian: "South Indian names often follow a different pattern with initials representing father/village name, followed by given name.",
    westIndian: "West Indian names (Gujarati, Maharashtrian) often have specific endings like Patel, Shah, Mehta, and distinctive first names.",
    eastIndian: "East Indian names (Bengali, Oriya) often have characteristic endings like -jee, -kar, and distinctive sounds."
  },
  
  // Common name validation rules
  validationRules: [
    "Check if names contain non-Indian characters like å, ø, ü, ñ that are not used in Indian languages",
    "Verify if the name follows common Indian naming patterns",
    "Check for common misspellings of Indian names",
    "Look for common prefix/suffix patterns in Indian names",
    "Validate regional variations in spelling (north vs south Indian names)",
    "Check for proper gender-specific name patterns"
  ]
};

const AuditDataForIndianNamesInputSchema = z.object({
  data: z
    .array(z.record(z.string()))
    .describe('An array of data records, where each record is a map of string to string.'),
});
export type AuditDataForIndianNamesInput = z.infer<typeof AuditDataForIndianNamesInputSchema>;

const AuditDataForIndianNamesOutputSchema = z.object({
  anomalies: z.array(
    z.object({
      row: z.number().describe('The row number where the anomaly was found.'),
      column: z.string().describe('The column name where the anomaly was found.'),
      value: z.string().describe('The value that was flagged as an anomaly.'),
      message: z.string().describe('The reason why the value was flagged as an anomaly.'),
      suggestion: z.string().optional().describe('A suggested correction for the anomaly.'),
    })
  ),
});
export type AuditDataForIndianNamesOutput = z.infer<typeof AuditDataForIndianNamesOutputSchema>;

export async function auditDataForIndianNames(
  input: AuditDataForIndianNamesInput
): Promise<AuditDataForIndianNamesOutput> {
  return auditDataForIndianNamesFlow(input);
}

const auditDataForIndianNamesFlow = ai.defineFlow(
  {
    name: 'auditDataForIndianNamesFlow',
    inputSchema: AuditDataForIndianNamesInputSchema,
    outputSchema: AuditDataForIndianNamesOutputSchema,
  },
  async input => {
    const anomalies: AuditDataForIndianNamesOutput['anomalies'] = [];
    
    console.log(`Auditing ${input.data.length} records for Indian name validation`);

    // Process each row of data
    for (let i = 0; i < input.data.length; i++) {
      const row = input.data[i];
      
      // Only check name columns
      const nameColumns = ['Accused First Name', 'Accused Last Name'];
      
      for (const column of nameColumns) {
        if (Object.prototype.hasOwnProperty.call(row, column)) {
          const value = row[column];
          
          // Skip empty values
          if (!value || value.trim() === '') continue;
          
          console.log(`Validating name: "${value}" in column: ${column}, row: ${i+1}`);
          
          // Validate the name using our enhanced service
          const nameInfo = await validateIndianName(value);
          
          if (!nameInfo.isValid) {
            console.log(`Found invalid name: "${value}" - ${nameInfo.message}`);
            
            anomalies.push({
              row: i + 1,
              column: column,
              value: value,
              message: nameInfo.message || 'The value is potentially not a valid Indian name.',
              suggestion: nameInfo.suggestion || '',
            });
          }
        }
      }
    }
    
    console.log(`Audit complete. Found ${anomalies.length} anomalies.`);

    return {anomalies};
  }
);
